@import url(https://fonts.googleapis.com/css2?family=Gentium+Book+Basic&family=Playfair+Display:wght@400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.time,
.material-icons {
  vertical-align: middle;
  font-size: 13px;
  color: red;
}
.hiddenbar {
  position: fixed;
  background-color: white;
  width: 100%;
  top: 0px;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  transition: all 0.5s ease;
}

.navcontainer {
  border-bottom: 0.4px solid rgb(172, 172, 172);
  padding: 10px;
  padding-bottom: 0px;
  transition: all 0.5s ease;
}
.navbar {
  text-align: center;
  position: relative;
}
.title {
  font-family: "Bodoni Moda", serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -1px;
  word-spacing: 5px;
  padding-bottom: 10px;
}
.navlist {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: rgb(131, 131, 131);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}
.navitem {
  padding: 10px 10px;
}
.navitem:hover {
  color: rgb(48, 48, 48);
  background-color: rgb(241, 241, 241);
  text-shadow: 0px 0px 1px black;
}
.searchpanel {
  position: absolute;
  right: 1vw;
  top: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  color: rgb(78, 78, 78);
  font-size: 14px;
}
.subscribe {
  margin-bottom: 20px;
}
.subscribe span {
  padding: 5px 10px;
}
.subscribe span:first-child {
  border-right: 0.5px solid rgb(161, 161, 161);
}
.search span {
  vertical-align: middle;
  font-size: 30px;
  margin-left: 10px;
}
.searchicon {
  position: absolute;
  top: 10px;
  right: 1vw;
  visibility: hidden;
}
@media screen and (max-width: 1300px) {
  .searchpanel {
    display: none;
  }
  .searchicon {
    visibility: visible;
  }
}

.newscontainer {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0px 5vw;
  margin-bottom: 20px;
  border-bottom: 0.4px solid rgb(172, 172, 172);
  font-family: "Gentium Book Basic", serif;
  font-size: 16px;
  color: rgb(78, 78, 78);
}
.newscontainer > div {
  padding: 0px 10px;
  border-right: 0.4px solid rgb(172, 172, 172);
}
.newscontainer > div:last-child {
  border: none;
}
.newscontainer img {
  width: 200px;
  object-fit: contain;
}

/* Main Headline/ Main content*/
.mainimage img {
  width: 100%;
}
.maintitle {
  font-family: "Playfair Display", serif;
  font-weight: 600;
  font-size: 24px;
  color: black;
  text-align: center;
  cursor: pointer;
}
.maintitle:hover {
  color: rgb(39, 97, 221);
}
.mainheading,
.articleheading,
.shortarticles {
  padding: 10px 0px;
}

.timecontainer {
  margin-top: 8px;
  font-family: "Gentium Book Basic", serif;
}
.mainheading,
.articleheading,
.shortarticles {
  border-bottom: 0.4px solid rgb(172, 172, 172);
}
.articleheading:last-child,
.shortarticles:last-child {
  border: none;
}
/* articleheading */
.articleheading .articletitle {
  font-family: "Playfair Display", serif;
  font-size: 16px;
  font-weight: 800;
  color: black;
  margin-bottom: 10px;
  cursor: pointer;
}
.column1 .articleheading:first-child .articletitle {
  font-size: 28px;
}
.articlecontent img {
  height: 100px;
  float: right;
}
/* short articles */
.shortimage {
  text-align: center;
}
.shorttitle {
  color: black;
  font-family: "Playfair Display", serif;
  font-weight: 700;
  cursor: pointer;
}
.articleheading .articletitle:hover,
.shorttitle:hover {
  color: rgb(39, 97, 221);
}

.loadingcontainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 70vmin;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 20px;
  gap: 20px;
}

table {
  width: 100%;
  border-top: 0.4px solid rgb(172, 172, 172);
  padding: 10px 0px;
  border-collapse: collapse;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;
}
tbody:hover {
  background-color: rgb(223, 223, 223);
  cursor: pointer;
}
td {
  padding: 7px 0px;
  border-bottom: 0.3px solid rgb(207, 207, 207);
}
table > tbody > tr > td:first-child {
  width: 40%;
}
table > tbody > tr > td:nth-child(n + 2) {
  text-align: end;
}
tbody.active {
  font-weight: 600;
  background-color: rgb(223, 223, 223);
  color: black;
}
.watchlist {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px 0px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: rgb(5, 132, 182);
}
.watchlist > div {
  cursor: pointer;
}
.watchlist > div:hover {
  color: blue;
}
.material-icons {
  color: grey;
  font-size: 20px;
}

.chartheader {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  padding: 0px 10px 10px 10px;
  border-bottom: 0.5px solid rgb(161, 161, 161);
}
.chartheader div {
  cursor: pointer;
}
.chartheader div:first-child {
  color: black;
  text-shadow: 1px 0px 0px black;
}
.chartheader div:hover {
  color: black;
  text-shadow: 1px 0px 0px black;
}
.chart {
  display: -webkit-flex;
  display: flex;
}
.duration {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px;
  padding-right: 0px;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}
.duration > div:first-child {
  color: black;
  text-shadow: 1px 0px 0px black;
}
.duration > div:hover {
  color: black;
  text-shadow: 1px 0px 0px black;
}
/* Opinions */
.opinioncontainer {
  padding-top: 10px;
  border-top: 0.4px solid rgb(172, 172, 172);
}
.opinioncontainer > div:first-child {
  font-family: Arial, Helvetica, sans-serif;
  color: #a28c6d;
  font-size: 16px;
  font-weight: 600;
}
.opinioncontainer > div:nth-child(n + 2) {
  border-bottom: 0.4px solid rgb(172, 172, 172);
}
.opinioncontainer > div:nth-child(n + 2) > p:first-child {
  font-family: "Playfair Display", serif;
  font-size: 16px;
  font-weight: 600;
  color: black;
  padding: 10px 0px 5px 0px;
}
.opinioncontainer > div:nth-child(n + 2) > p:last-child {
  font-style: italic;
  color: #a28c6d;
  font-size: 16px;
  padding-bottom: 10px;
}

